<template>
<div id="rentCar">
   <h2>商家如何新建商品？</h2>
  <p>在工作台-其他内点击商品管理，完成商品创建。</p>
  <p>车辆名称及主图：用户在APP端看到的商品名称、商品图片，请确保车辆名称准确、图片清晰无广告。电池类型：该车辆使用电池类型；电池颗数：该车辆可租用1颗还是2颗电池。租赁类型：目前可选择多种租赁类型，支持日、周、月、季度、年及自定义。租赁方式：长租，支持商品日、周、月、年等租赁方式；短租：支持按分钟、按小时、按日计费。车辆押金：免押金指用户下单时不需要支持押金费用，存在一定风险；有押金指用户必须支付押金方可租用车辆，若支持芝麻信用，则用户芝麻分符合640以上则无需交押金即可租车。若不符合则只能交押金租车。</p>
  <p>新增的商品需要提交e换电运营人员审核，审核通过后方可上架。</p>
  <p>已上架的商品支持下架，下架后用户无法通过租车平台看到该商品，无法下单。</p>
  <div class="imgDiv">
    <img src="../../assets/m4.jpg" alt="">
  </div>
</div>
</template>

<script>
    export default {
        name: "rentCar"
    }
</script>

<style scoped lang="scss">
#rentCar{
 padding: 0 40px;
  box-sizing: border-box;
  h2{
    font-size:28px;
    font-family:PingFangSC-Medium;
    font-weight:500;
    color:rgba(51,51,51,1);
    line-height: 100px;
    border-bottom: 1px solid #F0F0F0;
    text-align: left;
  }
  p{
    font-size:28px;
    font-family:PingFangSC-Regular;
    font-weight:400;
    color:rgba(72,72,72,1);
    line-height:40px;
    margin-top: 38px;
    text-align: left;
  }
  .imgDiv{
    width:400px;
    margin: 40px auto;
    margin-top: 70px;
    img{
      height: 100%;
      width: 100%;
    }
  }
}
</style>



